import { LitElement, css, html, customElement, property, state } from "@umbraco-cms/backoffice/external/lit";
import { UmbElementMixin } from "@umbraco-cms/backoffice/element-api";

interface SpacingValue {
    top?: string;
    right?: string;
    bottom?: string;
    left?: string;
}

@customElement('altigen-spacing-editor')
export class AltigenSpacingEditorElement extends UmbElementMixin(LitElement) {

    @state()
    private _value: SpacingValue = {};

    @property({ attribute: false })
    public config: any;

    @property({ type: String })
    public set value(value: string | undefined) {
        if (!value) {
            this._value = {};
            return;
        }
        try {
            // Try parse as JSON
            this._value = JSON.parse(value);
        } catch {
            // Fallback: If it's a space separated string (e.g. "10px 20px")
            // We could try to parse CSS shorthand, but for now let's assume JSON is the source of truth
            // or just init empty if invalid JSON
            this._value = {};
        }
    }

    public get value(): string {
        return JSON.stringify(this._value);
    }

    private _update(side: keyof SpacingValue, val: string) {
        this._value = { ...this._value, [side]: val };
        this._dispatchChange();
    }

    private _dispatchChange() {
        this.dispatchEvent(new CustomEvent('property-value-change', { bubbles: true, composed: true }));
    }

    render() {
        return html`
            <div class="spacing-container">
                <div class="input-group">
                    <uui-label>Top</uui-label>
                    <uui-input 
                        .value=${this._value.top ?? ''} 
                        @input=${(e: any) => this._update('top', e.target.value)}
                        placeholder="0px">
                    </uui-input>
                </div>
                <div class="input-group">
                    <uui-label>Right</uui-label>
                    <uui-input 
                        .value=${this._value.right ?? ''} 
                        @input=${(e: any) => this._update('right', e.target.value)}
                        placeholder="auto">
                    </uui-input>
                </div>
                <div class="input-group">
                    <uui-label>Bottom</uui-label>
                    <uui-input 
                        .value=${this._value.bottom ?? ''} 
                        @input=${(e: any) => this._update('bottom', e.target.value)}
                        placeholder="0px">
                    </uui-input>
                </div>
                <div class="input-group">
                    <uui-label>Left</uui-label>
                    <uui-input 
                        .value=${this._value.left ?? ''} 
                        @input=${(e: any) => this._update('left', e.target.value)}
                        placeholder="auto">
                    </uui-input>
                </div>
            </div>
        `;
    }

    static styles = css`
        .spacing-container {
            display: grid;
            grid-template-columns: repeat(4, 1fr);
            gap: 10px;
            max-width: 600px;
        }
        .input-group {
            display: flex;
            flex-direction: column;
            gap: 5px;
        }
        uui-input {
            width: 100%;
        }
    `;
}

export default AltigenSpacingEditorElement;

declare global {
    interface HTMLElementTagNameMap {
        'altigen-spacing-editor': AltigenSpacingEditorElement;
    }
}
